import React from 'react';
import './App.css';
import './DragAndDrop.css';
import ReactDOM from 'react-dom';
import ReactTreeApp from './ReactTreeApp';

import DialogContainer from './DialogContainer.js';
import DisplayCode from './DisplayCode.js'
import code from './code-playground.js';
import * as d3 from "d3";

function Card(props) {

  const dragStart = e => {
    const target = e.target;
//    e.dataTransfer.setData('card_id', target.id);
    e.dataTransfer.setData('text', target.id);


    setTimeout(() => {
//      target.style.display = 'none';
    }, 0);
  }

  const dragOver = e => {
    e.stopPropagation();
  }

  const mouseOver = (e) => {
    e.target.style.cursor='pointer';
  }

  const dragEnd = (e) => {
    console.log('dragEnd');
  }

  return (
    <div id={props.id} className={props.className} draggable={props.draggable} onDragStart={dragStart} onDragOver={dragOver} onDragEnd={dragEnd} onMouseOver={mouseOver}>
      {props.id}
    </div>
  )

}


function Board(props) {

  const drop = (e) => {
    e.preventDefault();

    const card_id = e.dataTransfer.getData('text');
    const card = document.getElementById(card_id);
    card.style.display =  'block';
    e.target.appendChild(card);

  }

  const dragOver = (e) => {
    e.preventDefault();
  }

  const mouseOver = (e) => {
    e.target.style.cursor='pointer';
  }

  return(
    <div  id={props.id} className={props.className} onDrop={drop} onDragOver={dragOver} >
      {props.children}
    </div>
  )

}

class DragAndDrop extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return(
      <div id='mainDiv'>
        <Board id='board1' className='board'>
          <Card id={'card1'} className={'card'} draggable={'true'}>
            {/* <img src={process.env.PUBLIC_URL + '/images/chair2.jpg'} alt="Pineapple" style={{width:'200px', height:'200px'}}></img> */}
          </Card>            
          <Card id={'card2'} className={'card'} draggable={'true'}>
            {/* <img src={process.env.PUBLIC_URL + '/images/chair2.jpg'} alt="Pineapple" style={{width:'200px', height:'200px'}}></img> */}
          </Card>            
        </Board>  
        <Board id='board2' className='board'>
          <Card id='card3' className='card' draggable='true'>
          </Card>            
        </Board>  
      </div>
    )
  }
}

class DragAndDropApp extends React.Component {
    constructor(props) {
    super(props);
    this.goBack = this.goBack.bind(this);
    this.openDisplayCode = this.openDisplayCode.bind(this);
    this.onCancelDisplayCode = this.onCancelDisplayCode.bind(this);
    this.state = {
      showCode: false
    };
  }

  componentDidMount() {
  }

  goBack() {
    ReactDOM.render(<ReactTreeApp/>, document.getElementById('root'));
  }

  openDisplayCode() {
    this.setState({showCode: true});
  }

  onCancelDisplayCode() {
    this.setState({showCode: false});
  }

  render() {
    // let screenWidth = window.screen.width;
    // let screenHeight = window.screen.height;

    return (
      <div style={{height: '100%', position: 'absolute', width: '100%'}}>
        <div style={{height: '5%', textAlign: 'center', backgroundColor: 'lightGreen', paddingTop: '15px', paddingBottom: '15px'}}>
          <span><button onClick={this.goBack}>Back</button><label style={{fontSize: '24pt', marginLeft: '50px', marginRight: '50px'}}>D3</label><button onClick={this.openDisplayCode}>Code</button></span>
        </div>
        <DialogContainer show={this.state.showCode} onCancel={this.onCancelDisplayCode} dialogContent={<DisplayCode 
          code={code} onCancel={this.onCancelLogout}/>}
          width='1000px' height='700px' title={'D3Example.js'}>
        </DialogContainer>

        <div style={{height: '90%', position: 'absolute', width: '100%'}}>
          <DragAndDrop></DragAndDrop>
        </div>    

     </div>
    )
  }
}

export default DragAndDropApp;